.gallery {
  padding: 50px 20px;

  &__wrapper {
    @include section-content__size;
  }

  &__title {
    @include section-title;
  }

  &__title::after {
    @include section-title__after;
  }
}

.location-list {
  margin-top: 60px;

  &__photos {
    display: grid;
    column-gap: 20px;
    margin-bottom: 24px;
    margin-top: 40px;
  }

  &__item:first-child .location-list__photos {
    grid-template-columns: 2fr 1fr;
  }

  &__item:nth-child(2) .location-list__photos {
    grid-template-columns: 1fr 1fr;
  }

  &__item:last-child .location-list__photos {
    grid-template-columns: 1fr 2fr;
  }
    
  &__img {
    object-fit: cover;
    border-radius: $border-radius;
    height: 256px;
    width: 100%;
    justify-self: stretch;
  }

  &__img:hover {
    cursor: zoom-in;
  }

  &__desc {
    font-weight: bold;
    font-size: 16px;
    color: $text-color-darken;
  }

  &__link {
    color: $accent-color-light;
  }

  &__button-more {
    font-weight: bold;
    font-size: 16px;
    line-height: 52px;
    color: $accent-color-light;
    border: 2px solid $accent-color-light;
    padding: 0 65px;
    border-radius: $border-radius;
    transition: color 0.2s, background-color 0.2s;
    display: block;
    margin: 40px auto;
    background-color: transparent;
  }

  &__button-more:hover {
    color: #fff;
    background-color: $accent-color-light;
    cursor: pointer;
  }
}
